<template>
    <div class="myMenu">
        <!-- 菜单顶部 -->
        <div class="Tops"></div>

        <!-- 轮播图 -->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="item in banArr" :key="item">
                    <img v-lazy="item" />
                </van-swipe-item>
        </van-swipe>

        <!-- 菜单 -->
        <van-tree-select height="55vw" :items="items" :main-active-index.sync="active" :click-nav="ff_item(active)">
        <template #content>
               <van-card
                    v-for="(val,index) in lists"
                    :key="index"
                    :price="val.price"
                    :desc="val.enname"
                    :title="val.name"
                    :thumb="val.smallImg"
                    :origin-price="val.price-0+16"
                    @click="ff_goPages('Details',val.pid)"
                    >   
                        <template #tags>
                                <van-icon name="plus" />
                        </template>
                        
                    </van-card>
                   
        </template>
        </van-tree-select>
    </div>
</template>

<script>
import '../assets/less/Menu.less'
    export default {
        name:'Menu',
        data() {
            return {
                 active : 0,
                 banArr : [require('../assets/img/bar_01.png'),require('../assets/img/bar_02.png'),require('../assets/img/bar_03.png')],
                 items  : [{ text: '最新推荐' }, 
                          {  text: '咖啡'     }, 
                          {  text: '瑞幸冰'   }, 
                          {  text: '拿铁'     }],
                 lists  : [],
            } 
        },
        created() {
             this.$toast.loading({
                message: '   加载中...  ',
                forbidClick: true,
                loadingType: 'spinner',
            });
        
        },
        methods: {
            ff_item(val){
                if(val == 0){
                  this.ff_value('isHot',1);
                }else if(val == 1){
                  this.ff_value('type','coffee');
                }else if(val == 2){
                  this.ff_value('type','rena_ice');
                }else{
                  this.ff_value('type','latte');
                }
            },
            ff_value(key,value){
                  this.axios({
                    url:'/typeProducts',
                    method:'GET',
                    params:{
                        appkey : this.appkey,
                        key,
                        value,
                    }
                })
                .then(res=>{
                    if(res.data.code == 500){
                        this.lists = res.data.result;
                    }
                })
                .catch(err=>{
                     
                })
            },
             // 跳转模块
            ff_goPages(name,pid=null){
                this.$router.push({name,query:{pid}});
            }
         
           
        },
    }
</script>

<style lang="less" scoped>
  
</style>